export type DialogsType = typeof initialState
const FOLLOW = "FOLLOW"
const UNFOLLOW = "UNFOLLOW"
const SET_USERS = "SET-USERS"

export type UserType = {
    id: number,
    photos:PhotosType
    followed: boolean,
    name: string,
    status: string


}
type PhotosType = {
    Small: string
    Big: string
}
type UserLocation = {
    city: string,
    country: string,
}
